import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import * as rootSelects from '../store/reducers';
import * as rootActions from '../store/actions';

import {Panel, List, Group, PanelHeader, Cell, PullToRefresh} from '@vkontakte/vkui';
import Icon24Chevron from '@vkontakte/icons/dist/24/chevron';

import User from '../components/User';

const useFetchedUser = (dispatch) => {
	useEffect(() => {
		const {
			userActions: {fetchedUser}
		} = rootActions;

		dispatch(fetchedUser());
	}, []);
};

const Home = (props) => {
	const {id, go, fetchedUser, dispatch} = props;
	const [isFetching, setIsFetching] = useState(false);
	useFetchedUser(dispatch);

	const onRefresh = () => {
		setIsFetching(true);
		setTimeout(() => {
			setIsFetching(false);
		}, 2000);
	};

	return(
		<Panel id={id}>
			<PanelHeader>Главная</PanelHeader>
			
			<PullToRefresh
				onRefresh={onRefresh}
				isFetching={isFetching}>
				<Group title="">
					<User size={48} fetchedUser={fetchedUser} go={go} />
				</Group>

				<Group title="">
					<List>
						<Cell
							onClick={go}
							data-to='friends'
							asideContent={<Icon24Chevron />}
						>Друзья</Cell>
						<Cell
							onClick={go}
							data-to='search'
							asideContent={<Icon24Chevron />}
						>Поиск</Cell>
					</List>
				</Group>
			</PullToRefresh>
		</Panel>
	);
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => {
	const {
		selectUser: {getUser}
	} = rootSelects;

	return {
		fetchedUser: getUser(state)
	};
};

export default connect(mapStateToProps, null)(Home);
