import React, {useState} from 'react';
import {connect} from 'react-redux';

import * as rootSelects from '../../store/reducers';
import * as rootActions from '../../store/actions';

import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Footer,
	PullToRefresh,
	Group,
	List,
	Spinner,
	Cell,
	Search
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';


const SearchContainer = (props) => {
	const {id, go, dispatch, fetchedSearch} = props;
	const osname = platform();
	const [isFetching, setIsFetching] = useState(false);
	const [search, setSearch] = useState('');
	const {
		searchActions: {fetchenSearch}
	} = rootActions;

	const onRefresh = () => {
		setIsFetching(true);
		setTimeout(() => {
			setIsFetching(false);
		}, 2000);
	};

	const handleSearch = (q) => {
		setSearch(q);
		dispatch(fetchenSearch({
			q,
			filters: 'friends',
			fields: 'crop_photo'
		}));
	};

	console.log(fetchedSearch);

	return (
		<Panel id={id}>
			<PanelHeader
				left={<HeaderButton onClick={go} data-to="home">
					{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
				</HeaderButton>}
			>Поиск</PanelHeader>

			<Search value={search} onChange={handleSearch}/>

			<PullToRefresh
				onRefresh={onRefresh}
				isFetching={isFetching}>

				{!fetchedSearch.length && !isFetching && <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
					<Spinner size="regular" style={{ marginTop: 20 }} />
				</div>}

				{fetchedSearch.length > 0 &&
					<List>
						{fetchedSearch.map(item => (
							<Cell
								key={item.profile.id}
								description={item.description}
								onClick={() => window.open(`https://vk.com/id${item.profile.id}`)}
							>
								{`${item.profile.first_name} ${item.profile.last_name}`}
							</Cell>))}
					</List>}

				<Footer>{fetchedSearch.length > 0 ? `найдено ${fetchedSearch.length}` :'пусто'}</Footer>
			</PullToRefresh>
		</Panel>
	);
};

const mapStateToProps = (state) => {
	const {
		selectSearch: {getSearch}
	} = rootSelects;

	return {
		fetchedSearch: getSearch(state)
	};
};

export default connect(mapStateToProps, null)(SearchContainer);