import {FETCHED_SEARCH} from './actionType';
import {fetchedSearch} from '../../utils/api/vk';

export const fetchenSearch = ({q, filters, fields}) => async (dispatch) => {
	const search = await fetchedSearch({
		q,
		filters,
		fields
	});
	console.log('search', search);
	dispatch({type: FETCHED_SEARCH, search: [...search]});
};